import confirmPassword from '@nextcloud/password-confirmation';
import sortBy from 'lodash/fp/sortBy';
import AddDevice from './AddDevice';
import Device from './Device';
import logger from '../../logger';
import { removeRegistration } from '../../service/WebAuthnRegistrationSerice';
const sortByName = sortBy('name');
export default {
  components: {
    AddDevice,
    Device
  },
  props: {
    initialDevices: {
      type: Array,
      required: true
    },
    isHttps: {
      type: Boolean,
      default: false
    },
    isLocalhost: {
      type: Boolean,
      default: false
    },
    hasPublicKeyCredential: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      devices: this.initialDevices
    };
  },

  computed: {
    sortedDevices() {
      return sortByName(this.devices);
    }

  },
  methods: {
    deviceAdded(device) {
      logger.debug("adding new device to the list ".concat(device.id));
      this.devices.push(device);
    },

    async deleteDevice(id) {
      logger.info("deleting webauthn device ".concat(id));
      await confirmPassword();
      await removeRegistration(id);
      this.devices = this.devices.filter(d => d.id !== id);
      logger.info("webauthn device ".concat(id, " removed successfully"));
    }

  }
};